.sidebar {
  width: 16%;
  height: 100vh;
  background-color: #191d28;
  position: fixed;
  border-right: 1px solid #2e344e;
}

.main-content {
  width: 84%;
  margin-left: 16%;
  background-color: #10121b;
  min-height: 100vh;
  background-image: url(../img/dots.svg);
  display: grid;
  position: relative;
}

.main-content .content {
  margin: 3.2rem 10rem;
}
