.navbar .nav {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.navbar {
  height: 100vh;
}

.navbar .nav .profile {
  text-align: center;
  width: 100%;
  margin-top: 1rem;
  border-bottom: 1px solid #2e344e;
}

.navbar .nav .profile img {
  width: 70%;
  border-radius: 100%;
  border: 5px solid #2e344e;
  margin-bottom: 1rem;
}

.navbar .nav .nav-items .nav-item {
  list-style: none;
  text-align: center;
}

.navbar .nav .nav-items {
  width: 100%;
}

.navbar .nav .nav-items .nav-item a {
  text-decoration: none;
  font-size: inherit;
  color: inherit;
  text-align: center;
  display: block;
  padding: 0.5rem 0;
  position: relative;
}
.navbar .nav .nav-items .nav-item a::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0.4rem;
  height: 100%;
  background-color: #0381ff33;
  -webkit-transform-origin: bottom;
          transform-origin: bottom;
  -webkit-transform: scale(0);
          transform: scale(0);
  overflow-x: hidden;
}

.navbar .nav .nav-items .nav-item a:hover::before {
  width: 100%;
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: 0.2s width 0.5s cubic-bezier(1, -0.16, 0, 1.35), -webkit-transform 0.6s;
  transition: transform 0.6s, 0.2s width 0.5s cubic-bezier(1, -0.16, 0, 1.35);
  transition: transform 0.6s, 0.2s width 0.5s cubic-bezier(1, -0.16, 0, 1.35), -webkit-transform 0.6s;
}

.navbar .nav .footer {
  width: 100%;
  border-top: 1px solid #2e344e;
}

.navbar .nav .footer .p {
  text-align: center;
  padding: 1rem 0;
}

.active {
