.Title {
  text-transform: uppercase;
  font-size: 2rem;
  color: white;
  position: relative;
  padding-bottom: 1rem;
  letter-spacing: 3px;
}

.Title span {
  position: absolute;
  top: 15%;
  left: 0;
  font-size: 5rem;
  opacity: 0.07;
}

.Title::after {
  content: "";
  bottom: 0;
  position: absolute;
  left: 0;
  background-color: #037fff;
  width: 4rem;
  height: 0.4rem;
  border-radius: 50px;
}

.Title::before {
  content: "";
  bottom: 0;
  position: absolute;
  left: 0;
  background-color: #0381ff49;
  width: 6rem;
  height: 0.4rem;
  border-radius: 50px;
}

.ImageSection {
